export default {
	data() {
		return {
			currentIndex: null,
			isshow:false,
			titles: ["档次价格", "位置区域", "筛选"]
		};
	},
	methods: {
		select(index) {
			if(this.currentIndex==index){
				this.isshow=!this.isshow
			}else{
				this.isshow=true
			}
			this.currentIndex = index
		}
	}
}
